import React from 'react';
import styles from './me.module.scss';
import pp from '../../../static/pp.jpg';
import Anime from 'react-anime';
import { useSpring, animated } from 'react-spring';
import SocialNetworks from '../Icons/SocialNetworks';

export default function Me() {
  return (
    <div className={styles.container}>
      <div className={styles.center}>
        <div className={styles.ppContainer}>
          <Animated>
            <img className={styles.pp} src={pp} alt="Me" />
          </Animated>
        </div>
        <Anime opacity={[0, 1]} translateY={'15px'} delay={(e, i) => i * 1000}>
          <div className={styles.description}>
            Soy Luis Bernardo Garca Lpez, aka Berna, Ingeniero en Sistemas
            Computacionales. Actualmente trabajando en{' '}
            <a href="https://definityfirst.com">Definity First</a> como Web
            Engineer. <br />
            Tengo 4 aos de experiencia en desarrollo web los cuales 3 han sido
            Home Office, soy un fanboy de React , he publicado 4 libreras en{' '}
            <a href="https://www.npmjs.com/~bernagl">NPM</a> aunque actualmente
            estan descontinuadas por falta de tiempo, siempre disponible para un
            side project, an juego Tibia y he creado este blog para compartir
            mi aprendizaje sobre JavaScript y React.
          </div>
          <div className={styles.socialNetworksContainer}>
            <SocialNetworks></SocialNetworks>
          </div>
        </Anime>
      </div>
    </div>
  );
}

const calc = (x, y) => [
  -(y - window.innerHeight / 2) / 20,
  (x - window.innerWidth / 2) / 20,
  1.1,
];
const trans = (x, y, s) =>
  `perspective(600px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`;

function Animated({ children }) {
  const [props, set] = useSpring(() => ({
    xys: [0, 0, 1],
    config: { mass: 5, tension: 350, friction: 100 },
  }));
  return (
    <animated.div
      onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
      onMouseLeave={() => set({ xys: [0, 0, 1] })}
      style={{ transform: props.xys.interpolate(trans) }}
    >
      {children}
    </animated.div>
  );
}
